import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import Swal from "sweetalert2";
import "./FindAccountPage.css";

export default function FindAccountPage() {
  const [email, setEmail] = useState("");
  const [isPassword, setIsPassword] = useState(false);  //   or  
  const navigate = useNavigate();

  const handleFindAccount = () => {
    const users = JSON.parse(localStorage.getItem("users") || "[]");
    const user = users.find(u => u.id === email || u.email === email);

    if (!user) {
      Swal.fire({
        icon: 'error',
        title: '  ',
        text: '      ',
      });
      return;
    }

    if (isPassword) {
      //    (:     )
      Swal.fire({
        icon: 'success',
        title: '  ',
        text: '    ',
      });
    } else {
      //    (:   )
      Swal.fire({
        icon: 'success',
        title: '  ',
        text: ' : ${user.id}',
      });
    }

    navigate("/login");  // /     
  };

  return (
    <div className="find-account-container">
      <div className="find-account-box">
        <h2>{isPassword ? " " : " "}</h2>
        <input
          type="email"
          placeholder="  "
          value={email}
          onChange={(e) => setEmail(e.target.value)}
        />
        <div className="find-account-options">
          <label>
            <input
              type="radio"
              name="findType"
              checked={isPassword}
              onChange={() => setIsPassword(true)}
            />
             
          </label>
          <label>
            <input
              type="radio"
              name="findType"
              checked={!isPassword}
              onChange={() => setIsPassword(false)}
            />
             
          </label>
        </div>
        <button onClick={handleFindAccount}>{isPassword ? " " : " "}</button>
      </div>
    </div>
  );
}
